import React, { Component } from 'react';
import {
    BrowserRouter as Router,
    Route,
    Switch
} from 'react-router-dom';
import Container from './container';

class App extends Component {
  render() {
    return (
        <Router className='app'>
            <div>
                <Switch>
                    <Route path="/" component={Container.Home.view} />
                </Switch>
            </div>
        </Router>
    );
  }
}

export default App;

